import React, { useContext, useState, useEffect, useCallback } from 'react';
import { useHistory } from "react-router-dom";
import { doc, getDoc } from 'firebase/firestore';
import context from '../component/Context';
import mnd from '../assets/logo.svg';

const App = (props) => {
  const state = useContext(context);
  const { setUser, setYear } = state;
  const [view, setView] = useState(false);
  const [inputs, setInputs] = useState({
    number: "",
    pw: "",
  });
  const { number, pw } = inputs;
  const history = useHistory();

  //     
  useEffect(() => {
    const storedUser = localStorage.getItem('user');
    const storedYear = JSON.parse(localStorage.getItem('year'));

    if (storedUser && storedYear) {
      setUser(storedUser);
      setYear(storedYear);
    } else {
      setUser(null);
      setYear(null);
    }
  }, [setUser, setYear]);

  //   
  const onChange = useCallback((e) => {
    const { name, value } = e.target;
    setInputs((prevInputs) => ({
      ...prevInputs,
      [name]: value || "",
    }));
  }, []);

  //  
  const onCheck = async () => {
    const docRef = doc(props.manage, "ini");
    const docSnap = await getDoc(docRef);

    if (docSnap.exists()) {
      const data = docSnap.data();
      if (number === data.adminID && pw === data.adminPW) {
        setUser(number);
        setYear(data.year);

        localStorage.setItem('user', number);
        localStorage.setItem("year", JSON.stringify(data.year));

        history.push('/');
      } else {
        setInputs({
          number: '',
          pw: ''
        });
      }
    } else {
      console.log("No such document!");
    }
  };

  return (
    <div className='container'>
      <div className='login'>
        <div className='visual'>
          <div className='visualText'>
            <div className='textGroup'>
              <span></span><span></span><span></span><span></span>
              <span></span><span></span>
            </div>
            <img className='visualLogo' src={mnd} alt={'logo'} />
          </div>
        </div>
        <div>
          <form onSubmit={(e) => { e.preventDefault(); }}>
            <div className='armyWrap'>
              <div className={'input'}>
                <input
                  className={'id'}
                  type="text"
                  id='number'
                  name="number"
                  maxLength={12}
                  placeholder=""
                  onChange={onChange}
                  value={number}
                />
              </div>
              <div className={'input'}>
                <input
                  className={'pw'}
                  type={view ? 'text' : 'password'}
                  id='pw'
                  name="pw"
                  maxLength={12}
                  placeholder=""
                  onChange={onChange}
                  autoComplete="off"
                  value={pw}
                />
                <button
                  className='passView'
                  type="button"
                  onClick={() => setView(!view)}
                  title="pass view"
                >
                  <i className={view ? "ri-eye-off-line" : "ri-eye-line"}></i>
                </button>
                <span className={'vali'}>
                  {number === "" && pw === "" 
                    ? '   ' 
                    : number === 'fail' 
                    ? '  ' 
                    : pw === 'fail' 
                    ? ' ' 
                    : pw === 'same' 
                    ? '  ' 
                    : ''}
                </span>
              </div>
            </div>
            <div className='controll'>
              <button className={'button sign'} type="button" onClick={onCheck}>
                
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}

export default App;
